.o-slider-container {
  position: relative;
}
.o-slider {
  @include list-reset();
  position: relative;

  &__wrapper {
    @include clearfix();
    overflow: hidden;
  }

  &__arrow {
    cursor: pointer;
    height: 60px;
    margin-top: -32px;
    position: absolute;
    top: 50%;
    width: 60px;
    z-index: 100;
    border: 2px solid get-color(unique, white);
    background: transparent;
    box-shadow: none;
    -webkit-appearance: none;
    color: transparent;
    transform: translateY(-50%);
    transition: all .3s ease-in-out;

    &:hover {
      background: get-color(unique, white);

      &:before {
        color: get-brand(primary);
      }
    }

    &:before {
      @extend %icon-defaults;
      left: 50%;
      position: absolute;
      top: 50%;
      color: get-color(unique, white);
      @include font-size(2.8);
      transform: translate(-50%, -50%);
    }

    &--prev {
      left: 80px;

      &:before {
        content: get-icon('arrow-left');
      }
    }
    &--next {
      right: 80px;

      &:before {
        content: get-icon('arrow-right');
      }
    }
  }

  &__item {
    float: left;
    margin-top: 0;
    opacity: .3;
    padding-left: 60px;
    position: relative;
    transition: opacity .3s ease-in-out;

    .o-bubble-conversation {
      left: 0;
      margin-top: -32px;
      position: absolute;
      top: 50%;
      width: 400px;

      @include respond(xLarge) {
        transform: translate(-50%, -50%);
      }
    }

    .o-bubble {
      opacity: 0;
      transform: scale(0);
      transition: all .3s ease-in-out .5s;

      &:nth-child(2) {
        transition-delay: .75s;
      }
    }

    &.active {
      opacity: 1;

      .o-bubble {
        opacity: 1;
        transform: scale(1);
      }
    }
  }
  &__caption {
    @include font-size(2.3);
  }
}