.o-logo {
  display: inline-block;
  height: 77px;
  overflow: hidden;
  width: 150px;
  background: url(../images/smartsteuer-logo.png) no-repeat center / 100%;
  font-size: 0;
  line-height: 0;
}

.o-logo-avatar {
  display: inline-block;
  height: 100px;
  width: 100px;
  border-radius: $global-rounded-border-radius;
  background: url(../images/hauke.jpg) no-repeat center / 100%;
}