// 1.Config

// 2.Base
.o-decor {
  margin-top: 20px;
  position: relative;
  text-align: center;

  &:first-child {
    margin-top: 0;
  }

  &:before {
    @extend %icon-defaults;
    content: get-icon(micro);
    display: inline-block;
    padding: 25px;
    position: relative;
    z-index: get-index(decor);
    border: 1px solid $global-border-color;
    border-radius: $global-rounded-border-radius;
    background: get-color(unique, white);
    box-shadow: 0 0 15px rgba(get-color(unique, black), .1);
    color: get-brand(primary);
    @include font-size(5);
    font-weight: bold;
    line-height: 1;
  }

  &__pulse {
    position: absolute;
    border-radius: 50%;
    display: inline-block;
    left: 50%;
    width: 120px;
    height: 120px;
    top: 50%;
    border: 1px solid get-brand(primary);
    border-color: transparent transparent get-brand(primary) transparent;
    transform: translate(-50%, -50%) scale(1) rotate(90deg);
  }
  &__pulse--1 {
    opacity: 0;
    animation: decor-pulse-first 2s infinite .6s;
  }
  &__pulse--2 {
    height: 170px;
    opacity: 0;
    width: 170px;
    animation: decor-pulse-second 2s infinite .7s;
  }
  &__pulse--3 {
    height: 240px;
    opacity: .3;
    width: 240px;
    animation: decor-pulse-third 2s infinite;
  }
  &__pulse--top {
    border-color: get-brand(primary) transparent transparent transparent;
  }
}

.o-decor--chat {
  margin-left: -120px;

  &:before {
    content: get-icon(chat);
  }
}
.o-decor--chat--rotate {
  margin-left: 0;
  margin-right: -120px;
  margin-top: -50px;

  &:before {
    transform: scaleX(-1);
  }
}

// Animations for Decor
@keyframes decor-pulse-first {
  0%   { opacity: 0; }
  50%  { opacity: 1; }
  100% { opacity: 0; }
}

@keyframes decor-pulse-second {
  0%   { opacity: 0;  }
  50%  { opacity: .6; }
  100% { opacity: 0;  }
}

@keyframes decor-pulse-third {
  0%   { height: 0; opacity: 0; width: 0; }
  50%  { opacity: 1; }
  100% { height: 240px; opacity: 0; }
}