.o-quote {
  margin: 40px 0 0;
  padding: 40px 30px 20px 70px;
  position: relative;
  border-radius: $global-border-radius;
  box-shadow: 0 15px 60px rgba(get-color(unique, black), .1);
  color: get-color(gray);
  @include font-size(2.4);
  line-height: 1.3;
  font-style: italic;
  text-align: left;

  &:after {
    content: '';
    bottom: -12px;
    position: absolute;
    right: 50px;
    border-top: 12px solid get-color(unique, white);
    border-left: 12px solid transparent;
    border-right: 12px solid transparent;
  }

  &__avatar {
    height: 80px;
    left: -40px;
    position: absolute;
    top: -40px;
    width: 80px;
  }
  &__author {
    margin-top: 20px;
    color: get-color(gray, dark);
    @include font-size(2.1);
    text-align: right;
  }

  &__footer {
    display: table;
    text-align: right;

    > * {
      display: table-cell;
      margin-top: 0;
      padding-left: 15px;
      vertical-align: middle;
      text-align: right;

      &:first-child {
        padding-left: 0;
      }

      img {
        display: inline-block;
      }
    }
  }
}