.l-quotes {
  align-items: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  margin: 0 auto;
  max-width: 1680px;
  padding: 0 50px;

  @include respond(xLarge) {
    padding: 0 100px;
  }

  &__column {
    margin-top: 80px;

    @include respond(large) {
      padding: 0 50px;
      width: 50%;
    }

    @include respond(xLarge) {
      padding: 0 5%;
    }
  }
}