input,
textarea {
  display: block;
  padding: 0 0 5px;
  width: 100%;
  border: none;
  border-bottom: 2px solid get-color(unique, white);
  background: transparent;
  box-shadow: none;
  color: get-color(unique, white);
  @include font-size(1.8);
  font-weight: 300;
  line-height: 1;
  
  &:focus,
  &:active {
    outline: 0;
  }
  
  @include placeholder {
    color: get-color(unique, white);
    font-weight: 300;
  };
}

button[type="submit"] {
  cursor: pointer;
  border: none;
  background: transparent;
  box-shadow: none;
  color: get-color(unique, white);
  @include font-size(1.8);
  transition: all .3s ease-in-out;
  
  &:hover {
    transform: scale(.95);
  }
}

.o-form {
  display: flex;
  flex-direction: row;
  margin-top: 70px;
  
  > * {
    padding-left: 20px;
    
    &:first-child {
      padding-left: 0;
    }
  }
}