.o-button {
  align-items: center;
  cursor: pointer;
  display: inline-flex;
  justify-content: center;
  margin-top: 20px;
  padding: 14px 25px;
  vertical-align: baseline;
  border: none;
  border-radius: $global-border-radius;
  background: get-color(unique, white);
  box-shadow: none;
  -webkit-appearance: none;
  line-height: 1;
  color: get-color(blue);
  @include font-size(2);
  font-weight: bold;
  line-height: 1;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;

  &:hover {
    opacity: 0.9;
    text-decoration: none;
  }

  &:first-child {
    margin-top: 0;
  }

  i {
    padding-left: 10px;
    @include font-size(2.1);
    line-height: .7;

    &:first-child {
      padding-left: 0;
      padding-right: 10px;
    }
  }

  &--light {
    background-color: get-color(unique, white);
    color: get-color(blue);
  }
}

.o-button-fix {
  display: inline-block;
  // height: 250px;
  padding: 40px;
  vertical-align: top;
  // width: 250px;
  background: get-color(unique, white);
  box-shadow: 0 15px 60px rgba(get-color(unique, black), .1);
  text-align: center;
  transition: box-shadow .3s ease-in-out;

  &:hover {
    box-shadow: none;
    text-decoration: none;
  }

  i {
    display: block;
    height: 89px;
    margin: 0 auto 20px;
    width: 89px;
  }
  .icon-chat {
    position: relative;
    border-radius: $global-rounded-border-radius;
    background: #269cd8;
    color: get-color(unique, white);
    @include font-size(5);
    line-height: 0.6;

    &:before {
      left: 50%;
      position: absolute;
      top: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

.o-button-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 60px;
  text-align: center;

  @include respond(medium) {
    margin-left: -6%;
  }

  @include respond(large) {
    margin-left: -5%;
  }

  @include respond(xLarge) {
    padding: 0 20px;
  }

  > * {
    display: block;
    margin-top: 30px;
    width: 100%;

    @include respond(medium) {
      align-items: center;
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      justify-content: center;
      margin-left: 6%;
      width: 44%;
    }
    @include respond(large) {
      margin-left: 5%;
      width: 24%;
    }
  }
}