// 1.Config


// 2.Base
.l-banner {
  align-items: center;
  display: flex;
  justify-content: center;
  margin-bottom: 100px;
  padding: 0 0 450px;
  position: relative;
  background-color: get-color(blue);
  color: get-color(unique, white);
  text-align: center;

  @include respond(large) {
    margin-bottom: 300px;
  }

  &__top {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 40px 0 80px;
    position: relative;
    z-index: 100;

    @include respond(medium) {
      flex-direction: row;
      justify-content: space-between;
    }

    .o-button {
      align-self: center;
    }
  }

  &__cover {
    height: auto;
    left: 0;
    height: 100%;
    opacity: 0.2;
    position: absolute;
    top: 0;
    width: 100%;
    background: url(../images/smartsteuer-hero.jpg) no-repeat center / cover;
  }
  &__body {
    max-width: 740px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px;
    position: relative;
    z-index: 100;
  }

  &__phone {
    top: 60%;
    display: inline-block;
    height: 614px;
    left: 50%;
    position: absolute;
    width: 297px;
    transform: translateX(-50%);
    background: url(../images/smartphone-app.png) no-repeat center / 75%;

    @include respond(large) {
      background-size: 100%;
    }
  }

  h1 {
    color: get-color(unique, white);
  }
}
.l-main {
  padding-top: 80px;
}
.l-contentinfo {
  margin-top: 150px;
  padding-bottom: 50px;
  padding-top: 100px;
  background: get-color(gray, light);
  text-align: center;

  &__constrained {
    @extend %constrained-defaults;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include respond(medium) {
      flex-direction: row;
    }
  }
  &__bottom {
    @extend %constrained-defaults;
    align-items: center;
    display: flex;
    flex-direction: column;
    max-width: $max-wrapper-width;
    padding-top: 100px;
    @include font-size(1.5);

    @include respond(medium) {
      flex-direction: row;
      justify-content: space-between;
    }
  }
  a {
    color: get-font(color);
  }
  .o-navigation {
    margin-top: 10px;

    @include respond(medium) {
      margin-top: 0;
    }
  }
}